.nodis-partner {
    background-color: var(--hover-color);
    border: 1px solid var(--border-color);
    border-radius: 4px;
    // display: flex;
    align-items: start;
    transition: 0.3s ease-in;
    // cursor: pointer;
    .more-images {
        line-height: 16px;
        border-bottom: 1px solid var(--text-dim);
        cursor: pointer;
    }
    .poster-container {
        width: 100%;
        height: 100%;
        border-radius: 4px;
        background-position: center center;
        background-size: cover;
        background-repeat: no-repeat;
        cursor: zoom-in;
    }
    .content {
        display: flex;
        flex-direction: column;
        line-height: 1.25em;
        .title {
            color: var(--text-max);
            text-decoration: none;
            font-size: var(--fs-xl);
            // line-height: 1;
        }
        .desc {
            overflow: hidden;
            display: -webkit-box;
            // -webkit-line-clamp: 3;
            // line-clamp: 3;
            // -webkit-box-orient: vertical;
            // line-height: 1.25em;
            // min-height: 2.5em;
        }
    }
    &:hover {
        box-shadow: 0px 0px 5px -1px var(--primary-color);
    }
}


.partner-meta {
    background-color: var(--hover-color);
    .title {
        color: var(--text-max);
        font-size: 44px;
    }
    .img-container {
        overflow: hidden;
        padding: 4px;
        .partner-img {
            width: 100%;
            height: 250px;
            background-repeat: no-repeat;
            background-size: cover;
            background-position: center center;
            transition: 0.2s all ease-in-out !important;
            border-top-right-radius: 4px;
            border-top-left-radius: 4px;
        }
    }
}